"use client";

import Image from "next/image";
import Link from "next/link";
import React, { useEffect, useState } from "react";

const Header = () => {
  /////////////////////////////////////////// MOBILE MENU ////////////////////////////////////////////
  const [mobileOpen, setMobileOpen] = useState(false);

  /////////////////////////////////////////// STICKY TRANSPARENT NAV ////////////////////////////////////////////
  const [isSticky, setSticky] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setSticky(window.scrollY > 10);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header
      className={`sticky top-0 w-full z-10 transitions ${
        isSticky
          ? "bg-primary shadow-sm backdrop-blur-md text-white"
          : "bg-transparent text-primary"
      }`}
    >
      <div className="root-container flex items-center justify-between h-16">
        <div className="flex items-center gap-8">
          {/* LOGO  */}
          <Link href="/" className="flex items-center gap-1">
            {isSticky ? (
              <Image
                src="/logo/logo2.svg"
                alt="Hudle"
                width={200}
                height={200}
                className="size-7"
              />
            ) : (
              <Image
                src="/logo/logo.svg"
                alt="Hudle"
                width={200}
                height={200}
                className="size-7"
              />
            )}
            <span className="hidden lg:block text-2xl font-viga">Hudle</span>
          </Link>

          {/* DESKTOP NAV  */}
          <nav className="hidden lg:flex items-center gap-6">
            
          </nav>
        </div>
      </div>
    </header>
  );
};

export default Header;
